<template>
  <div>
    <section class="container">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Event Invitation</h1>
        </div>
      </div>
    </section>

    <section class="container section content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">Dear {{ recipient.name }}</p>
          <p>
            You are the recipient of a partner ticket to TEDxAuckland's May event
            at Whoa! Studios. You have X tickets to invite friends, colleagues or
            who ever you choose.
          </p>
          <p>
            During the first part of the evening you will be inspired and
            entertained by some of Aucklands top thought leaders. Following this
            will be social time with the speakers and a five course meal loveingly
            crafted with West Auckland produce by acclaimed and multi-award
            winning Kiwi chef, Ben Bayly
          </p>
          <p>
            This event is limited to 160 attendees where tickets are allocated
            through an applicaiton process.
          </p>
          <p>
            The people you invite however skip this application process, so don't
            underestimate the value of an invite.
          </p>
          <p>We look forward to putting on a great show.</p>
          <p>Elliot and the team at TEDxAuckland</p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="acceptTicket">
            <h2 class="title is-3">Your Contact Info</h2>

            <PersonInfo class="section-spacing" :person="person" :$v="$v" />
            <h2 class="title is-3">Your Details</h2>
            <PersonDetail class="section-spacing" :details="personDetail" :$v="$v" />

            <div class="buttons has-addons is-right">
              <button @click.prevent="rejectTicket" class="button is-warning is-medium">
                Reject Ticket
              </button>
              <input class="button is-primary is-medium" type="submit" value="Accept Ticket" />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/components/PersonInfo.vue";
import PersonDetail from "@/components/PersonDetail.vue";
import TicketService from "@/services/TicketService";
import personValidations from "@/validations/PersonValidations";
import personDetailValidations from "@/validations/PersonDetailValidations";

export default {
  name: "claim-ticket",
  components: {
    PersonInfo,
    PersonDetail
  },
  props: {
    recipient: {
      type: Object,
      required: true
    },
    ticket: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      person: this.recipient,
      personDetail: this.createFreshPersonDetail()
    };
  },
  validations: {
    person: personValidations,
    personDetail: personDetailValidations
  },
  methods: {
    acceptTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        TicketService.claimTicket(this.$route.params.id, {
          person: this.person,
          person_detail: this.personDetail
        })
          .then(() => {
            this.$router.push({
              name: "partner-invites",
              params: {
                id: this.$route.params.id
              }
            });
          })
          .catch(e => {
            console.log("ERROR", e);
          });
      }
    },

    rejectTicket() {
      console.log("Ticket Rejected", this.$route.params.id);
    },

    createFreshPersonDetail() {
      return {
        userId: this.userId,
        age: "",
        gender: "",
        eventsAttended: [],
        industry: "",
        suburb: "",
        jobTitle: "",
        organisation: "",
        educationLevel: "",
        conversationStarters: {
          one: "",
          two: "",
          three: ""
        }
      };
    }
  }
};
</script>

<style>
  .section-spacing {
    margin-bottom: 3rem;
  }
</style>
