import axios from "axios";

const baseURL = process.env.NODE_ENV === 'production'
  ? "https://ticketing.tedxauckland.com/api/v1"
  : "http://localhost:3000/api/v1"

const apiClient = axios.create({
  baseURL: baseURL,
  withCredentials: false,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
});

export default {
  submitApplication(data) {
    return apiClient.post("/ticket_applications/", data);
  },

  getTicketClaimInfo(token) {
    return apiClient.get("/tickets/claim/" + token);
  },
  claimTicket(token, data) {
    return apiClient.post("/tickets/claim/" + token, data);
  },
  rejectTicket(token) {
    return apiClient.post("/tickets/reject/" + token);
  },

  getTicketInfo(token) {
    return apiClient.get("/tickets/" + token);
  }
};
