<template>
  <div>
    <section class="container">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Event Ticket Applications</h1>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="applyForTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">Your Details</h2>
            <PersonDetail class="section-spacing" :details="personDetail" :$v="$v" />

            <h2 class="title is-3">Your Application</h2>
            <ApplicationDetails class="section-spacing" :application="application" :$v="$v" />
            <input class="button is-medium is-pulled-right is-primary" type="submit" value="Apply For Ticket" />
          </form>
        </div>
      </div>
    </section>
  </div>

</template>

<script>
import PersonInfo from "@/components/PersonInfo";
import PersonDetail from "@/components/PersonDetail";
import ApplicationDetails from "@/components/ApplicationDetails";
import TicketService from "@/services/TicketService";
import personValidations from "@/validations/PersonValidations";
import personDetailValidations from "@/validations/PersonDetailValidations";
import applicationValidations from "@/validations/ApplicationValidations";

export default {
  name: "apply",
  components: {
    PersonInfo,
    PersonDetail,
    ApplicationDetails
  },
  data() {
    return {
      person: this.createFreshTicketInfo(),
      personDetail: this.createFreshPersonDetail(),
      application: this.createFreshApplication()
    };
  },
  validations: {
    person: personValidations,
    personDetail: personDetailValidations,
    application: applicationValidations
  },
  methods: {
    applyForTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        TicketService.submitApplication({
          person: this.person,
          person_detail: this.personDetail,
          application: this.application
        })
          .then(() => {
            this.$router.push({
              name: "apply-thankyou"
            });
            this.person = this.createFreshTicketInfo();
            this.personDetail = this.createFreshPersonDetail();
            this.application = this.createFreshApplication();
          })
          .catch(e => {
            console.log("ERROR", e);
          });
      }
    },

    createFreshTicketInfo() {
      const id = Math.floor(Math.random() * 10000000);

      return {
        ticketId: id,
        name: "",
        email: ""
      };
    },

    createFreshPersonDetail() {
      return {
        userId: this.userId,
        age: "",
        gender: "",
        eventsAttended: [],
        industry: "",
        suburb: "",
        jobTitle: "",
        organisation: "",
        educationLevel: "",
        conversationStarters: {
          one: "",
          two: "",
          three: ""
        }
      };
    },

    createFreshApplication() {
      return {
        userId: this.userId,
        case: ""
      };
    }
  }
};
</script>

<style>
input {
  transition: all 200ms;
}

input.error,
select.error,
textarea.error {
  border-color: red !important;
}

div.error {
  border: 1px solid red;
  border-radius: 0.4rem;
}

.error-message {
  color: red;
}
</style>
