import Vue from "vue";
import Router from "vue-router";
import Apply from "./views/Apply";
import ClaimTicket from "./views/ClaimTicket";
import SendInvites from "./views/SendInvites";
import AcceptInvite from "./views/AcceptInvite";
import Thankyou from "./views/Thankyou";

import TicketService from "@/services/TicketService";
Vue.use(Router);

export default new Router({
  // mode: "history",
  base: "/events/05-2019/ticketing/",
  routes: [
    {
      path: "/apply",
      name: "apply",
      component: Apply
    },
    {
      path: "/apply/thankyou",
      name: "apply-thankyou",
      component: Thankyou
    },
    // {
    //   path: "/apply/:id",
    //   name: "application-status",
    //   component: ApplicationStatus
    // },
    {
      path: "/claim/:id",
      name: "claim-ticket",
      component: ClaimTicket,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketClaimInfo(routeTo.params.id).then(response => {
          const recipient = response.data.recipient;
          const ticket = response.data.ticket;
          const event = response.data.event;
          routeTo.params.recipient = recipient;
          routeTo.params.ticket = ticket;
          routeTo.params.event = event;
          next();
        });
      }
    },
    {
      path: "/claim/thankyou",
      name: "claim-thankyou",
      component: Thankyou
    },
    {
      path: "/ticket/:id",
      name: "partner-invites",
      component: SendInvites,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketInfo(routeTo.params.id).then(response => {
          const recipient = response.data.recipient;
          const ticket = response.data.ticket;
          const event = response.data.event;
          routeTo.params.recipient = recipient;
          routeTo.params.ticket = ticket;
          routeTo.params.event = event;
          next();
        });
      }
    },
    {
      path: "/invite/:id",
      name: "accept-invite",
      component: AcceptInvite
    }
  ]
});
